/*! 退休审批表 css */
.txspb {
  padding: 0;
  min-height: 100%;
  .content {
    padding: 0 @30px;
  }
  p {
    text-indent: 2em;
    font-size: @28px;
    line-height: @40px;
    color: #333;
    padding-bottom: @10px;
  }
  .img-box {
    padding: @14px @30px;
    img {
      display: block;
      max-width: 100%;
      height: auto;
      margin-bottom: @30px;
    }
  }
}

