/*! 材料补正 css */

.clbz {
  padding: 0;
  min-height: 100%;
  background-color: #f7f8f9;
  .top-fixed {
    position: static;
    background-color: transparent;
  }
  .before-line{
    padding-top: @30px;
  }
  ul {
    width: 100%;
    padding: 0 @30px;
  }
  li {
    background-color: #fff;
    height: @212px;
    border-radius: @10px;
    box-shadow: 0 0 @8px @8px #eee;
    margin-bottom: @30px;
  }
  .icon-file {
    width: @26px;
    height: @32px;
    background-image: url(../images/icon/icon-file.png);
    //   margin-right: @10px;
  }
  .title {
    padding: @36px @30px 0;
    span {
      display: block;
      line-height: 1;
      color: #333;
      font-size: @30px;
    }
  }
  .btn-box {
    padding: @50px @30px 0;
  }
  .btn {
    background-color: transparent;
    line-height: @56px;
    height: @60px;
    width: @300px;
    border: @2px solid #ddd;
    color: #999;
    font-size: @28px;
    font-weight: normal;
    &:last-of-type {
      color: #007cf7;
      border-color: #007cf7;
    }
  }
}
