/*! 在线缴费 css */
.online-payment {
  padding: 0;
  overflow: hidden;
  min-height: 100%;
  background-color: #fff;
  .info {
    padding-top: @68px;
    text-align: center;
    img {
      width: @86px;
      height: @86px;
      margin: auto;
    }
    span {
      display: inline-block;
      font-size: @56px;
      color: #333;
      padding-top: @30px;
      line-height: 1;
      font-weight: 500;
      &::before {
        content: "¥";
        font-size: @42px;
        line-height: 1;
        padding-right: @16px;
      }
    }
    p {
      font-size: @30px;
      line-height: 1;
      padding-top: @20px;
      color: #333;
    }
    &.result {
      padding-top: @154px;
      img {
        width: @202px;
        height: @150px;
      }
      p {
        color: #2cc70f;
        font-size: @36px;
        font-weight: 500;
      }
      .name {
        color: #999;
        font-size: @28px;
        padding-top: @50px;
        font-weight: normal;
      }
    }
  }
  .cell-box {
    padding: 0 @30px;
    padding-top: @70px;
  }
  .cell {
    height: @110px;
    line-height: 1;
    font-size: @28px;
    border-bottom: 1px solid #ddd;
    color: #333;
    .left {
      color: #999;
    }
  }
  .btn-box {
    padding: @60px;
    &.result {
      padding-top: @100px;
    }
  }
}
